import Vue from 'vue'
import Router from 'vue-router'
import PuppyCube from './../components/page/PuppyCube'
// import PuppyCubeS from './../components/page/PuppyCubeS'
import PuppyIndex from './../components/page/PuppyIndex'
import JoinUS from './../components/page/JoinUS'
import NewsCenter from './../components/page/NewsCenter'
import VideoCenter from './../components/page/VideoCenter'
import VideoDetail from './../components/page/VideoDetail'
import NewsDetail from './../components/page/NewsDetail'
import ContactUS from './../components/page/ContactUS'
import AboutUS from './../components/page/AboutUS'
import LegalNotices from './../components/page/LegalNotices'
import PrivacyProtection from './../components/page/PrivacyProtection'
import Cooperate from './../components/page/Cooperate'
import AIoT from './../components/page/AIoT'

Vue.use(Router)

export default new Router({
  mode: 'history',  
  routes: [
    { path: '/en/', name: 'PuppyIndex', component: PuppyIndex },
    { path: '/en/puppycube', name: 'PuppyCube', component: PuppyCube },
    // { path: '/puppycubes', name: 'PuppyCubeS', component: PuppyCubeS },
    { path: '/en/joinus', name: 'JoinUS', component: JoinUS },
    { path: '/en/newscenter', name: 'NewsCenter', component: NewsCenter },
    { path: '/en/videocenter', name: 'VideoCenter', component: VideoCenter },
    { path: '/en/videodetail', name: 'VideoDetail', component: VideoDetail},
    { path: '/en/newsdetail', name: 'NewsDetail', component: NewsDetail},
    { path: '/en/contact', name: 'ContactUS', component: ContactUS},
    { path: '/en/aboutus', name: 'AboutUS', component: AboutUS},
    { path: '/en/legalnotices', name: 'LegalNotices', component: LegalNotices},
    { path: '/en/privacyprotection', name: 'PrivacyProtection', component: PrivacyProtection},
    { path: '/en/cooperate', name: 'Cooperate', component: Cooperate},
    { path: '/en/aiot', name: 'AIoT', component: AIoT},
  ],

  scrollBehavior: function (to, from, savedPosition) {
    return savedPosition || { x: 0, y: 0 }
  }
  
})



