<template>
  <div id="pagefooter">
      <div class="pagefooterTop">
        <div class="pagefooterMid">
            <div class="footerInfo">
                <div class="footerLeft">
                    <div class="footerLeft_div">
                        <span>Crowdfunding</span>
                        <div class="sub_title">
                            <router-link to="/en/">
                            <a href="" target="_blank">Indiegogo</a>
                            </router-link>
                        </div>
                    </div>
                    <div class="footerLeft_div">
                        <span>Site map</span>
                        <div class="sub_title">
                            <router-link to="/en/legalnotices">
                                <a herf="#">Legal Notices</a>
                            </router-link>
                        </div>
                        <div class="sub_title">
                            <router-link to="/en/privacyprotection">
                                <a herf="#">Privacy Protection</a>
                            </router-link>
                        </div>
                    </div>
                    <div class="footerLeft_div">
                        <span>About puppy</span>
                        <div class="sub_title">
                            <router-link to="/en/aboutus">
                                <a herf="#">Company Profile</a>
                            </router-link>
                        </div>
                    </div>
                </div>
                <div class="footerMid">
                    <!-- <img src="./../../assets/images/logo_black.png"> -->
                </div>
            
                <div class="footerRight">
                    <a href="https://www.instagram.com/puppycubeai/" target="_blank"><img src="./../../assets/images/footer/instagram.png"></a>
                    <!-- <a href="" target="_blank"><img src="./../../assets/images/footer/Youtube.png"></a> -->
                    <a href="https://www.facebook.com/puppycubeAI/?modal=admin_todo_tour" target="_blank"><img src="./../../assets/images/footer/facebook.png"></a>
                    <a href="https://twitter.com/puppyrobot3" target="_blank"><img src="./../../assets/images/footer/twitter.png"></a>
                </div>

            </div>
        </div>
      </div>
    <div class="recordInfo">
        <div class="footerLeft_re"></div>
        <div class="footerMid_re">Copyright © 2018 Guangzhou puppy robotics Co., Ltd. All rights reserved.</div>
        <div class="footerRight_re">
            <div class="selector">
                <img src="./../../assets/images/footer/english.png">
            </div>
            <div class="selector">
                <select v-model="currentLanguage" @change="languageChanged()">
                    <option value="1">中文</option>
                    <option value="2">English</option>
                </select>
            </div>
           
        </div>
        
    </div>
  </div>
</template>
<script>
export default {
    data () {
        return {
            currentLanguage: 2,  //英文站默认为2
        }
    },
    methods: {
        languageChanged() {
            if(this.currentLanguage == 1) {
                if(window.location.host == "test.hachismart.com") {
                    window.location.href = "http://test.hachismart.com";
                }else {
                    window.location.href = "http://www.hachismart.com";
                }
            } else {
                if(window.location.host == "test.hachismart.com") {
                    window.location.href = "http://test.hachismart.com/en/";
                }else {
                    window.location.href = "http://www.hachismart.com/en/";
                }
            }
        }

    }
}
</script>


<style lang="scss" scoped>
    $footerHeight: 260px;
    $footerColor: #ffffff;

    #pagefooter {
        width: 100%;
        z-index: -1;
        font-size: 14px;
        // margin-top: 20px;
        
    }

    .pagefooterTop {
        width: 100%;
        text-align: center;
        background-color: $footerColor;
    }

    .pagefooterMid {
        width: 1280px;
        margin: 0 auto;
    }

    .footerInfo {
        height: $footerHeight;
        width: 100%;
        background-color: $footerColor;
        padding-top: 70px;
        box-sizing: border-box;
    }

    .recordInfo {
        background-color: white;
        height: 80px;
        font-size: 12px;
        width: 1280px;
        margin: 0 auto;
        position: relative;
        line-height: 80px;
        border-top: 1px solid #d6d6d6;
    }

    .footerLeft_re,.footerRight_re {
        width: 30%;
        height: 80px;
        text-align: right;
        float: left;
        line-height: 80px;
        img {
            // margin-top: 25px;
            margin-bottom: -10px;
        }

        select {
            cursor: pointer;
            border: none;
            margin-right: 15px;
        }
    }

    .footerMid_re {
        width: 40%;
        height: 80px;
        float: left;
    }

    .selector {
        float: right;
        line-height: 80px;
    }

    .footerLeft {
        // height: $footerHeight;
        float: left;
        width: 40%;
        .footerLeft_div {
            width: 150px;
            height: 140px;
            float: left;
            text-align: left;
        }

        span {
            padding-top: 70px;
            font-weight: bold;
        }

        .sub_title {
            margin-top: 20px;
            font-size: 14px;
        }
    }

    .footerMid {
        float: left;
        width: 20%;


        .footerMid_1 {
            width: 100%;

            .footerMid_img {
            width: 100%;
            margin-top: 20px;
            height:140px;
            float: left;
            text-align: center;
            }
        }
    }

    .footerRight {
        // height: $footerHeight;
        float: right;
        text-align: right;
        width: 40%;

        img {
            margin-left: 48px;
            cursor: pointer;
        }
        
    }

    a {
        text-decoration-line: none;
        color: black;
        :hover {
            color: #929497;
        }
    }

    .service {
        // padding-top: 20px;
        line-height: 25px;
    }

    a:link{text-decoration:none;}
    a:visited{text-decoration:none;}
    a:active{text-decoration:none;}
</style>



