<template>
  <div id='pageheader'>
    <div :class="this._isMobile()? 'pageheaderMid_mobile' : 'pageheaderMid'">
        <div id='divLogo'>
            <router-link to="/en/">
            <img id='pageheaderlogo' @click="isDisplay?mobileMenuDisplay():''" :class="this._isMobile()?'logo_margin':''" src="./../../assets/images/header/puppyheaderlogo.svg">
            </router-link>
        </div>
        <div id='divPCNav' v-show="!_isMobile()">
            <ul>
                <li class="puppycube_style" @mouseover="cubeSecMenuDisplay()" @mouseout="cubeSecMenuHide()">
                    <a herf="">puppy cube</a>
                    <div class="divCubeSecMenu">
                        <ul>
                            <router-link to="/en/puppycube">
                            <li>
                                <img src="./../../assets/images/icon_cube.png">
                                <a herf="">puppy cube</a>
                            </li>    
                            </router-link>
                        </ul>
                    </div>
                </li>
                <li>
                    <a herf="">AI-based solution</a>
                    <ul>
                        <router-link to="/en/aiot">
                        <li>
                            <a herf="">AIoT Tech</a>
                        </li>  
                        </router-link>
                    </ul>
                </li>
                <li>
                    <a herf="">Crowdfunding</a>
                    <ul>
                        <router-link to="/en/">
                        <li>
                            <a herf="">Indiegogo</a>
                        </li>  
                        </router-link>
                    </ul>
                </li>
                <li>
                    <a herf="">About puppy</a>
                    <ul>
                        <router-link to="/en/aboutus">
                        <li>
                            <a herf="">Company Profile</a>
                        </li>
                        </router-link>
                        <router-link to="/en/contact">
                        <li>
                            <a herf="">Contact</a>
                        </li>
                        </router-link>
                         <router-link to="/en/newscenter">
                        <li>
                            <a herf="">Media Coverage</a>
                        </li>
                        </router-link>
                        <router-link to="/en/videocenter">
                        <li>
                            <a herf="">Product Video</a>
                        </li>
                        </router-link>
                    </ul>
                </li>
            </ul>
        </div>

    </div>
  </div>
</template>

<script>

    export default {
        name: 'PageHeader',
        data () {
            return {
                isDisplay: false,
                cubeSecMenuDisp: false,
                menu1Display: false,
                menu2Display: false,
                menu3Display: false,
            }
        },

        methods: {
            _isMobile() {
                let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
                return flag;
            },
            
            cubeSecMenuDisplay: function() {
                this.cubeSecMenuDisp = true;
            },

            cubeSecMenuHide: function() {
                this.cubeSecMenuDisp = false;
            },

           

        }
    }
</script>

<style scoped lang="scss" type="text/css">
    $headerHeight: 46px;
    $menuSec: 44px;
    $headerColor: #f1f1f1;

    #pageheader {
        background-color: $headerColor;
        // opacity: 0.9;
        width: 100%;
        height: $headerHeight;
        z-index: 9999;
        text-align: center;
    }

    .pageheaderMid {
        width: 1280px;
        margin: 0 auto;
    }

    .pageheaderMid_mobile {
        width: 100%;
    }

    #divLogo {
        float: left;
        // margin-left: 30px;
        height: $headerHeight;
    }

    #pageheaderlogo {
        height: 28px;
        margin-top: $headerHeight/2 - 28px/2; 
    }

    .logo_margin {
        margin-left: 10px;
    }

    #divPCNav {
        float: right;
        // margin-right: 30px;
    }

    li { 
        display: inline-block;
        cursor: pointer;
        line-height: $headerHeight;
        padding-left: 30px;
        // padding-right: 15px;
        right: 0;
        top: 0px;
        bottom: 0px;
        width: 130px;
        text-align: left;
        list-style-type: none;
        a {
            font-size: 14px;
        }
        
    } 

    .puppycube_style {
        padding-left: 45px !important;
        width: 115px;
        
    }

    li:hover {
        background-color:white;
        // width: 110px;
        // box-shadow: 1px 1px 1px #e6e6e6;
        // opacity: 1;
    }

    ul li:hover>a{
        color: #929497;
    }

    ul li {
        position: relative;
    }

    ul {
        margin: 0px;
    }

    ul ul {
        position: absolute;
        top: $headerHeight;
        height: 190px;
        text-align: center;
        display: none;
        z-index: 9999;
        background-color: white;
        box-shadow: 2px 2px 2px #e6e6e6;
        margin-left:-30px;
        width: 160px;
        padding-top: 15px;
        padding-bottom: 15px;
        box-sizing: border-box;
        line-height: 39px;

    }

    ul li:hover > ul {
        display: block;
    }

    ul li:hover .divCubeSecMenu{
        display: block;
    }

    ul ul li {
        width: 130px;
        height: 40px;
        position: relative;
        background-color: white;
        display: inline-block;
        line-height: 40px !important;
        list-style-type: none;
        z-index: 9999;
        margin-top: 0px; 
        margin-bottom: 0px;
        margin-left: -40px;
        // background-color: red;
         a {
            font-size: 13px;
        }
    }

    .divCubeSecMenu {
        position: fixed;
        top: $headerHeight;
        margin-top: -1px;
        border-top: none;
        left: 0px;
        padding: 0px;
        text-align: center;
        width: 100%;
        height: 180px;
        z-index: 9999;
        display: none;
        box-sizing: border-box;
        // box-shadow: 1px 1px 1px #e6e6e6;

        img {
            margin: 30px 10px -30px -12px;
        }
        ul li {
            width: 120px !important;
        }
    }

    .divCubeSecMenu ul {
        width: 100%;
        height: 190px;
        position: absolute;
        margin: 0px;
        background-color: white;
        display: inline-block;
        line-height: $menuSec !important;
        padding-left: 15px;
        padding-right: 15px;
        right: 0;
        top: 0px;
        bottom: 0px;
        list-style-type: none;
        // opacity: 0.9;
        z-index: 9999;
    }

    .divCubeSecMenu ul li {
        width: 100%;
        height: $menuSec;
        position: relative;
        margin: 0px;
        background-color: white;
        display: inline-block;
        line-height: $menuSec !important;
        padding-left: 15px;
        padding-right: 15px;
        right: 0;
        top: 0px;
        bottom: 0px;
        list-style-type: none;
        // opacity: 0.9;
        z-index: 9999;
    }

    a {
        font-size: 14px;
        color: black;
        text-decoration: none;
        font-family: 'Pingfang SC','Microsoft YaHei',Arial,Helvetica,sans-serif; 
    }

    .line-t {
        position: absolute;
        top: $headerHeight;
        margin-right: 0px;
        padding: 0px;
        text-align: center;
        width:100%;
    }

    .line-b {
        position: absolute;
        top: $headerHeight;
        margin-right: 0px;
        padding: 0px;
        text-align: center;
        width:100%;
    }

    .line-t .mobileMenuLine {
        -webkit-transform: translateY(-8px); 
        transform: translateY(-8px); 
    }

    .line-b .mobileMenuLine {
        -webkit-transform: translateY(8px); 
        transform: translateY(8px); 
    }

    .menu_sublist_1,.menu_sublist_2,.menu_sublist_3{
        display:none;
        // width: 96%;
        height: 110px;
        margin-top: 4px;
        padding: 25px;
        background-color: rgba(255,255,255, 0.1);
    }

    .m_con {
        width: 100%;
        height: 70px;
    }

    .mtitle {
        float: left;
        height: 70px;
    }

    .arrow_contianer {
        height: 35px;
        padding-top: 31px;
        padding-right: 20px;
        float: right;
    }

    .arrow_style {
        width: 15px;
        float: right;
    }

    .icon_con {
        width: 100px;
        float: left;
    }

    .icon {
        width: 50px;
        opacity: 0.5;
        margin-left: 26px;
        // float: left;
    }

    .icon_title {
        width: 100px;
        text-align: center;
        color: white;
        margin-top: 5px;
        opacity: 0.5;
    }

    .space {
        float: left;
        height: 50px;
        width: 20px;
    }

</style>




